import VanillaTilt from "vanilla-tilt";

const element2 = document.querySelector('section');

if (element2.classList.contains('press')) {

	const allCards = document.querySelectorAll(".press__item");

	allCards.forEach(i => {
		VanillaTilt.init(i, {
			max: 5,
			speed: 3000,
			perspective: 1000
		});
	})
}
