const element = document.querySelector('section');

if (element.classList.contains('mission')) {
	let swiper = new Swiper("#mission-slider", {
		pagination: {
			el: ".swiper-pagination",
			clickable: true,
		},
		autoplay: {
			delay: 3500,
			disableOnInteraction: false,
		},
		speed: 800,
	});

}
